.cart-dropdown {
    position: absolute;
    width: 240px;
    height: 340px;
    display: flex;
    flex-direction: column;
    padding: 20px;
    border: 1px solid black;
    background-color: white;
    top: 90px;
    right: 40px;
    z-index: 5;
    .custom-button{
        min-width: 165px;
        width: auto;
        height: 50px;
        letter-spacing: 0.5px;
        font-size: 11px;
        line-height: 50px;
        padding: 0 35px 0 35px;
        background-color: black;
        color: white;
        text-transform: uppercase;
        font-family: 'Open Sans';
        font-weight: bolder;
        border: none;
        cursor: pointer;
    }
    .cart-items {
        height: 100%;
        display: flex;
        flex-direction: column;
        overflow-y: auto;

     
    
    }
  
    button {
      margin-top: auto;
    }
  }